.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;

  .embed-responsive-item,
  iframe,
  embed,
  object,
  video {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    border: 0;
  }
}

.aspect-ratio-square {
  padding-top: 100%;
}
.aspect-ratio-16\/9 {
  padding-top: 56.25%;
}
.aspect-ratio-4\/3 {
  padding-top: 75%;
}
.aspect-ratio-21\/9 {
  padding-top: 42.86%;
}
